.hero {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #f2f2f2;
}

.heroContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}

.heroDescriptionContainer {
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: flex-start;
}

.awardContainer {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 5px 10px;
    background-color: #fff;
    border-radius: 40px;
}

.awardImageWrapper {
    border: 1px solid #8bbb97;
    border-radius: 50%;
    padding: 5px;
}

.awardImage {
    width: 10px;
    height: 10px;
}

.awardText {
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 0.02em;
}

.heroTitle {
    font-size: 38px;
    letter-spacing: 0.03em;
    line-height: 1.2;
}

.heroTitleSpan {
    color: #8bbb97;
}

.heroSubtitle {
    font-size: 18px;
    line-height: 1.2;
}

.heroShopLink {
    padding: 13px 18px;
    border-radius: 30px;
    font-weight: 600;
    letter-spacing: 0.03em;

    color: #fafafa;
    background-color: #8bbb97;
    transition: background-color 150ms linear;
}

.heroShopLink:hover,
.heroShopLink:focus {
    background-color: #75a280;
}

.statList {
    display: flex;
    gap: 15px;
}

.statItem:not(:last-child) {
    padding-right: 15px;
    border-right: 1px solid #8a8a8a;
}

.statCount {
    font-weight: 700;
    font-size: 27px;
    letter-spacing: 0.03em;
    margin-bottom: 3px;
}

.statText {
    font-size: 14px;
    color: #8a8a8a;
    letter-spacing: 0.02em;
}

.heroGalleryContainer {
    margin-left: auto;
    margin-right: auto;
}

.gallery {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: auto;
    height: 100%;
    transition: transform 0.5s ease-in-out;
    width: 100%;
    /* width: 400px; */
}

.galleryItem {
    /* width: 320px; */
    border: 1px solid #cbc9c9;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
}

.galleryImage {
    max-height: 200px;
    border-radius: 5px;
}

.galleryDescriptionContainer {
    margin-top: 15px;
    position: relative;
}

.galleryName {
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 0.02em;
    margin-bottom: 5px;
}

.galleryCount {
    color: #8a8a8a;
}

.galleryButton {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);

    background-color: transparent;
    border: none;
}

.galleryIcon {
    border-radius: 50%;
    padding-right: 4px;
    padding-bottom: 4px;
    
    background-color: #8bbb97;
    transition: background-color 150ms linear;
}

.galleryButton:hover .galleryIcon,
.galleryButton:focus .galleryIcon {
    background-color: #75a280;
}

.heroButtonsContainer {
    margin-top: 20px;
    display: flex;
}

.heroPrevButton,
.heroNextButton {
    border: none;
    background-color: transparent;
}

.heroPrevButton:hover .heroPrevIcon,
.heroPrevButton:focus .heroPrevIcon {
    background-color: #878787;
}

.heroNextButton:hover .heroNextIcon,
.heroNextButton:focus .heroNextIcon {
    background-color: #75a280;
}

.heroPrevIcon,
.heroNextIcon {
    border-radius: 50%;
    padding-right: 6px;
    padding-bottom: 8px;
    transition: background-color 150ms linear;
}

.heroPrevIcon {
    transform: rotate(225deg);
    background-color: #acacac;
}

.heroNextIcon {
    transform: rotate(45deg);
    background-color: #8bbb97;
    
}


@media screen and (min-width: 768px) {

    .heroContainer {
        display: flex;
        flex-wrap: nowrap;
        gap: 50px;
    }

    .heroDescriptionContainer {
        max-width: 50%;
        gap: 15px;
    }

    .heroTitle {
        line-height: 1.1;
    }

    .heroShopLink {
        padding: 10px 15px;
        margin-bottom: 10px;
    }

    .statText {
        font-size: 16px;
    }

    .heroGalleryContainer {
        margin: auto;
    }

    .heroButtonsContainer {
        margin-top: 30px;
    }

}


@media screen and (min-width: 1200px) {

    .heroContainer {
        max-width: 1200px;
    }
    
    .heroDescriptionContainer {
        min-width: 70%;
        gap: 25px;
    }

    .awardText {
        font-size: 14px;
    }

    .awardImageWrapper {
        padding: 7px;
    }

    .awardImage {
        width: 14px;
        height: 14px;
    }

    .heroTitle {
        font-size: 47px;
        width: 700px;
    }

    .heroSubtitle {
        font-size: 20px;
    }

    .heroShopLink {
        padding: 13px 20px;
        font-size: 18px;
    }
}